* {
  box-sizing: border-box;
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf");
  font-weight: 300;
}

@font-face {
  font-family: Avenir-bold;
  src: url("Avenir-Heavy.01d9ab74.ttf");
