.serch-bar {
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  flex-direction: column;
  width: 100%;
  margin-top: 20px;
  font-family: Avenir, sans-serif;
  display: flex;
}

.serch-bar::placeholder {
  color: #3d4e61;
}

.serch-bar:active {
  outline: none;
  font-family: Avenir-bold, sans-serif;
}

.serch-bar--big {
  background: url("Search.ccd3c07b.svg") 26px / 19px no-repeat;
  height: 70px;
  padding-left: 62px;
  font-size: 16px;
  box-shadow: 0 1px 8px #3d4e611a;
}

.serch-bar--big:hover {
  box-shadow: 0 4px 4px #00000040;
}

.serch-bar--big:focus {
  text-shadow: 0 4px 4px #00000040;
  background: url("Search.ccd3c07b.svg") 26px / 19px 19px no-repeat, linear-gradient(#fff 0%, #f6f6f7 100%);
}

.serch-bar--small {
  background: url("Search.ccd3c07b.svg") 13px / 11px 11px no-repeat;
  height: 42px;
  padding-left: 33px;
  font-family: Avenir, sans-serif;
  font-size: 14px;
  box-shadow: 0 1px 8px #3d4e611a;
}

.serch-bar--small:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}

.serch-bar--small:focus {
  background: url("Search.ccd3c07b.svg") 13px / 11px no-repeat, linear-gradient(#fff 0%, #f6f6f7 100%);
}
